import data from '../data/data.json';

function getDuration(duration) {
  const seconds = Math.floor((duration / 1000) % 60),
    minutes = Math.floor((duration / (1000 * 60)) % 60),
    hours = Math.floor((duration / (1000 * 60 * 60)) % 24);

  if (hours === 0 && minutes === 0) {
    return 'A few seconds';
  } else if (hours > 24) {
    return 'More than 1 day';
  } else {
    return `${hours ? `${hours}h ` : ''}${minutes}m ${seconds}s`;
  }
}

export const { project, summary } = data;

const { latest_completed_scan: ltsScan } = project;

export const contents = ['Basic Information', 'Component Breakdown', 'Full Component List'];

export const basicInfos = [
  { name: 'Scan Initiated', value: new Date(project.created).toDateString() },
  { name: 'Scan Duration', value: getDuration(new Date(ltsScan.scan_finish).getTime() - new Date(ltsScan.scan_start).getTime()) },
  { name: 'Scan Type', value: ltsScan.scan_type },
  { name: 'Project Name', value: project.fullname },
  { name: 'Project Source', value: project.provider },
  { name: 'Organization', value: project.org_name },
  { name: 'Team', value: project.team_name },
  { name: 'Triggered By', value: ltsScan.sender },
  { name: 'Project Description', value: project.description },
];

export const allComponents = data.issues.map((issue) => ({
  component: issue.library_version.library.name,
  version: issue.library_version.version_number,
  root: issue.root?.name || '',
  depth: issue.tree_level,
  ltsVersion: issue.latest_version.version_number,
  vulnerabilities: issue.vul_count,
  license: issue.library_version.license?.name || '',
  hasVulnerabilities: issue.vulnerability_level !== 'no vulnerabilities',
}));

export const vulComponents = allComponents.filter((issue) => issue.hasVulnerabilities);

export const chart = {
  title: 'Vulnerable Breakdown',
  data: [
    { name: 'Critical', value: summary.scan_issues_overview.critical_scan_issues },
    { name: 'High', value: summary.scan_issues_overview.high_scan_issues },
    { name: 'Medium', value: summary.scan_issues_overview.medium_scan_issues },
    { name: 'Low', value: summary.scan_issues_overview.low_scan_issues },
    { name: 'None', value: summary.scan_issues_overview.none_scan_issues },
  ],
};

export const formattedData = {
  cover: {
    title: 'Component Analysis Report',
    subtitle: project.fullname,
  },
  contents: contents,
  basicInfos: {
    data: basicInfos,
    themes: {
      logo: { display: 'block', width: '380px' },
      title: { paddingLeft: '20px', paddingRight: '20px', textTransform: 'capitalize' },
      h1: { color: 'grey' },
      h2: {},
    },
  },
  vulComponents,
  chart,
  allComponents
};
