import { saveAs } from 'file-saver';
import {
  Document,
  Packer,
  Header,
  TableOfContents,
  PageBreak,
  Paragraph,
  HeadingLevel,
  TextRun,
  ImageRun,
  Footer,
  PageNumber,
  AlignmentType,
  Table,
  TableCell,
  TableRow,
  BorderStyle,
  WidthType,
  HeightRule,
} from 'docx';
import { formattedData } from '../data/formattedData';
import logo from '../assets/images/scantistLogo.png';

async function generateDocx(echarts, opitons) {

  const lineBreaks = (length = 1) => Array.apply(null, { length }).map(() => new Paragraph(''));

  const pageBreak = () => [
    new Paragraph({
      children: [new PageBreak()],
    }),
  ];

  const topBottomBorder = {
    top: {
      style: BorderStyle.SINGLE,
      size: 3,
      color: '#333333',
    },
    bottom: {
      style: BorderStyle.SINGLE,
      size: 0,
      color: '#333333',
    },
    left: {
      style: BorderStyle.NIL,
      size: 0,
      color: '#333333',
    },
    right: {
      style: BorderStyle.NIL,
      size: 0,
      color: '#333333',
    },
  };

  const header = {
    default: new Header({
      children: [
        new Paragraph({
          alignment: AlignmentType.LEFT,
          children: [
            new TextRun(new Date().toLocaleDateString()),
          ],
        }),
      ],
    }),
  };

  const cover = () => [
    new Paragraph({
      children: [
        new ImageRun({
          data: logo,
          transformation: {
            width: 380,
            height: 120,
          },
        }),
      ],
    }),
    new Paragraph({
      text: formattedData.cover.title,
      border: {
        bottom: {
          color: 'auto',
          space: 1,
          value: 'single',
          size: 6,
        },
      },
    }),
    new Paragraph({
      text: formattedData.cover.subtitle,
    }),
  ];

  const contents = () => [
    new TableOfContents('Content', {
      hyperlink: true,
      headingStyleRange: '1-5',
    }),
  ];

  const basicInfos = () => {
    const rows = formattedData.basicInfos.data.map(
      (info) =>
        new TableRow({
          height: {
            value: opitons.rowHeight * 20,
            rule: opitons.rowHeight ? HeightRule.EXACT : HeightRule.AUTO,
          },
          children: [
            new TableCell({
              children: [new Paragraph(info.name)],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(info.value)],
              borders: topBottomBorder,
            }),
          ],
        })
    );

    return [
      new Paragraph({
        text: '1.' + formattedData.contents[0],
        heading: HeadingLevel.HEADING_1,
      }),
      ...lineBreaks(1),
      new Table({
        rows: [...rows],
      }),
    ];
  };

  const vulComponents = () => {
    const tableHeader = new TableRow({
      children: [
        new TableCell({
          children: [new Paragraph('#')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Component')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Version')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Root')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Depth')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Latest Version')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Vulnerabilities')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('License')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
      ],
    });

    const rows = formattedData.vulComponents.map(
      (component, index) =>
        new TableRow({
          height: {
            value: opitons.rowHeight * 20,
            rule: opitons.rowHeight ? HeightRule.EXACT : HeightRule.AUTO,
          },
          children: [
            new TableCell({
              children: [new Paragraph(index + 1 + '')],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['component'])],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['version'])],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['root'])],

              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['depth'] + '')],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['ltsVersion'])],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['vulnerabilities'].join(' '))],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['license'])],
              borders: topBottomBorder,
            }),
          ],
        })
    );
    return [
      new Paragraph({
        text: '1.1 Vulnerable Component List',
      }),
      ...lineBreaks(1),
      new Table({
        rows: [tableHeader, ...rows],
      }),
    ];
  };

  const charts = () => {
    return [
      new Paragraph({
        text: '2.' + formattedData.contents[1],
        heading: HeadingLevel.HEADING_1,
      }),
      ...lineBreaks(1),
      new Paragraph({
        children: [
          ...echarts.map(
            (echart) =>
              new ImageRun({
                data: Uint8Array.from(atob(echart), (c) => c.charCodeAt(0)),
                transformation: {
                  width: 300,
                  height: 185,
                },
              })
          ),
        ],
      }),
    ];
  };

  const allComponents = () => {
    const tableHeader = new TableRow({
      height: {
        value: opitons.rowHeight * 20,
        rule: opitons.rowHeight ? HeightRule.EXACT : HeightRule.AUTO,
      },
      children: [
        new TableCell({
          children: [new Paragraph('#')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Component')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Version')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Root')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Depth')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Latest Version')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('Vulnerabilities')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
        new TableCell({
          children: [new Paragraph('License')],
          borders: topBottomBorder,
          width: {
            size: 1440,
            type: WidthType.DXA,
          },
        }),
      ],
    });

    const rows = formattedData.allComponents.map(
      (component, index) =>
        new TableRow({
          height: {
            height: 150,
            rule: HeightRule.ATLEAST,
          },
          children: [
            new TableCell({
              children: [new Paragraph(index + 1 + '')],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['component'])],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['version'])],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['root'])],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['depth'] + '')],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['ltsVersion'])],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['vulnerabilities'].join(' '))],
              borders: topBottomBorder,
            }),
            new TableCell({
              children: [new Paragraph(component['license'])],
              borders: topBottomBorder,
            }),
          ],
        })
    );
    return [
      new Paragraph({
        text: '3.' + formattedData.contents[2],
        heading: HeadingLevel.HEADING_1,
      }),
      ...lineBreaks(1),
      new Table({
        rows: [tableHeader, ...rows],
      }),
    ];
  };

  const footer = {
    default: new Footer({
      children: [
        new Paragraph({
          alignment: AlignmentType.RIGHT,
          children: [
            new TextRun({
              children: [PageNumber.CURRENT, '/', PageNumber.TOTAL_PAGES],
            }),
          ],
        }),
      ],
    }),
  };

  const doc = new Document({
    features: {
      updateFields: true,
    },
    sections: [
      {
        headers: header,
        children: [
          ...lineBreaks(24),
          ...cover(),
          ...lineBreaks(),
          ...contents(),
          ...lineBreaks(),
          ...basicInfos(),
          ...pageBreak(),
          ...vulComponents(),
          ...lineBreaks(),
          ...charts(),
          ...lineBreaks(),
          ...allComponents(),
          ...lineBreaks(),
        ],
        footers: footer,
      },
    ],
  });

  Packer.toBlob(doc).then((blob) => {
    console.log(blob);
    saveAs(blob, 'example.docx');
    console.log('Document created successfully');
  });
}

export default generateDocx;
