import React from 'react';
import ReactECharts from 'echarts-for-react';
import { formattedData } from '../data/formattedData';

const legendData = formattedData.chart.data.map(({ name }) => name);

const Echart = ({ getChart }) => {
  const option = {
    animation: false,
    title: {
      text: formattedData.chart.title,
      x: 'center',
    },
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)',
    },
    legend: {
      orient: 'vertical',
      left: 'left',
      data: [...legendData],
    },
    series: [
      {
        name: formattedData.chart.title,
        type: 'pie',
        radius: '55%',
        center: ['50%', '60%'],
        data: formattedData.chart.data,
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)',
          },
        },
      },
    ],
  };

  function onChartReady(echarts) {
    console.log({ 'echarts is ready': echarts });

    getBase64Image(echarts);
  }

  function getBase64Image(echarts) {
    const base64 = echarts.getDataURL().split('base64,')[1];
    getChart(base64);
  }

  return (
    <>
      <ReactECharts
        option={option}
        style={{ height: 500 }}
        // opts={{renderer: 'svg'}}
        onChartReady={onChartReady}
      />
      <ReactECharts
        option={option}
        style={{ height: 500 }}
        // opts={{renderer: 'svg'}}
        onChartReady={onChartReady}
      />
    </>
  );
};

export default Echart;
