import React, { useState, useRef } from 'react';
import Cover from '../components/Cover';
import Content from '../components/Content';
import BasicInformation from './basicInformation';
import { project, basicInfos, contents } from '../data/formattedData';

import generateDocx from '../utils/exportDocx.js';
import Echart from '../components/Echart';

function PageView() {
  const [chartSrc, setChartSrc] = useState([]);
  const [settings, setSettings] = useState({ width: '0', rowHeight: '0' });

  const inputWidth = useRef(null);
  const inputRowHeight = useRef(null);

  function getEchart(base64Image) {
    setChartSrc((chartSrc) => [...chartSrc, base64Image]);
  }

  function handleClick() {
    const width = inputWidth.current.value || 0;
    const rowHeight = inputRowHeight.current.value || 0;
    setSettings({ width, rowHeight });
  }

  return (
    <>
      <div>
        width: <input ref={inputWidth} type='text' />
        pt
      </div>
      <div>
        table row height: <input ref={inputRowHeight} type='text' />
        pt
      </div>
      <button type='button' onClick={() => handleClick()}>
        Set
      </button>
      <button onClick={() => generateDocx(chartSrc, settings)}>Generate</button>
      <div id='html-content' style={{ width: '210mm', padding: '40px' }}>
        <Cover fullName={project.fullName} />
        <Content contents={contents} />
        <BasicInformation basicInfos={basicInfos} />
        <Echart getChart={getEchart} />
      </div>
    </>
  );
}

export default PageView;
